import FollowsYou from './FollowsYou'

const experiments = [
  {
    path: 'follows-you',
    component: FollowsYou,
    name: 'follows-you',
    meta: {
      title: 'Follows You',
      name: 'follows you'
    }
  }
]

export default experiments



